import { getOphan } from '../../client/ophan/ophan';
import { isServer } from '../../lib/isServer';
// ophan helper methods
const submitComponentEventTracking = async (componentEvent, renderingTarget) => {
    if (isServer)
        return;
    const ophan = await getOphan(renderingTarget);
    ophan.record({ componentEvent });
};
export const submitViewEventTracking = (componentEvent, renderingTarget) => void submitComponentEventTracking({
    ...componentEvent,
    action: 'VIEW',
}, renderingTarget);
const submitClickEventTracking = (componentEvent, renderingTarget) => void submitComponentEventTracking({
    ...componentEvent,
    action: 'CLICK',
}, renderingTarget);
export const withComponentId = (id) => ({
    componentType: 'SIGN_IN_GATE',
    id,
});
export const trackLink = (componentId, value, renderingTarget, abTest) => {
    const component = withComponentId(componentId);
    submitClickEventTracking({
        component,
        abTest,
        value,
    }, renderingTarget);
};
